import service from '../../service.js';
export default {
	data() {
		return {
			days: [],
			SignUp: [],
			cur_year: 0, //当前选的年
			cur_month: 0, //当前选的月
			today: parseInt(new Date().getDate()), //本日
			toMonth: parseInt(new Date().getMonth() + 1), //本月
			toYear: parseInt(new Date().getFullYear()), //本年
			weeks_ch: ['日', '一', '二', '三', '四', '五', '六'],
			weeks_en: ['Sun', 'Mon', 'Tues', 'Wed', 'Thur', 'Fri', 'Sat'],
			score:'',
		};
	},
	props: {
		sendYear: {
			type: Number,
			default: new Date().getFullYear()
		},
		sendMonth: {
			type: Number,
			default: new Date().getMonth() + 1
		},
		dataSource: { //已签到的数据源
			type: Array,
			default: () => {
				return []
			}
		},
		langType: { //只是示例一个翻译而已，要想所有都翻译自己可以再加加
			type: String,
			default: "ch"
		},
	},
	created() {
		this.cur_year = this.sendYear;
		this.cur_month = this.sendMonth;
		this.SignUp = this.dataSource;

		this.calculateEmptyGrids(this.cur_year, this.cur_month);
		this.calculateDays(this.cur_year, this.cur_month);
		this.onJudgeSign();
		
	},
	watch: {
		dataSource: 'onResChange',
	},
	methods: {
		getscore(){
			uni.request({
				url: 'http://10.0.0.22:8080/system/score/uniApp/getlist',
				method:'GET',
				data: {
					userphone:"15951920943",
					scoretime:this.cur_year+"-"+this.cur_month+'-'+1
					
				},
				success: (res) => {
					let row = res.data.rows;
					if(row > 0){
						this.score = row[0].score;
					}else{
						this.score = '';
					}
				}
			});
		},
		goMain(){
			uni.navigateTo({
			    url: './main'
			});
		},
		// 获取当月共多少天
		getThisMonthDays(year, month) {
			return new Date(year, month, 0).getDate()
		},
		// 获取当月第一天星期几
		getFirstDayOfWeek(year, month) {
			return new Date(Date.UTC(year, month - 1, 1)).getDay();
		},
		// 计算当月1号前空了几个格子，把它填充在days数组的前面
		calculateEmptyGrids(year, month) {
			//计算每个月时要清零
			this.days = [];
			const firstDayOfWeek = this.getFirstDayOfWeek(year, month);
			if (firstDayOfWeek > 0) {
				for (let i = 0; i < firstDayOfWeek; i++) {
					var obj = {
						date: null,
						isSign: false
					}
					this.days.push(obj);
				}
			}
			console.log(this.days);
		},

		// 绘制当月天数占的格子，并把它放到days数组中
		calculateDays(year, month) {

			const thisMonthDays = this.getThisMonthDays(year, month);
			// this.columnsLen=Math.ceil(thisMonthDays/7);
			// console.log(this.columnsLen);
			const user= service.getUsers();
			console.info(service.getUsers());
			uni.request({
				url: 'http://10.0.0.22:8080/system/score/uniApp/getlist',
				method:'GET',
				data: {
					userphone:user[user.length -1].account,
					scoretime:year+"-"+month+'-'+1
					
				},
				success: (res) => {
					if(res.statusCode == 200){
						let row = res.data;
						for(var i = 0;i<row.length;i++){
							this.days.push(row[i]);
						}
					}
				}
			});
			

		},

		onResChange(newD, oldD) {
			this.SignUp = newD;
			this.onJudgeSign();
		},
		//匹配判断当月与当月哪些日子签到打卡
		onJudgeSign() {
			var signs = this.SignUp;
			var daysArr = this.days;
			for (var i = 0; i < signs.length; i++) {
				var current = new Date(signs[i].replace(/-/g, "/"));
				var year = current.getFullYear();
				var month = current.getMonth() + 1;
				var day = current.getDate();
				day = parseInt(day);
				for (var j = 0; j < daysArr.length; j++) {
					//年月日相同则打卡成功   						
					if (year == this.cur_year && month == this.cur_month && daysArr[j].date == day) { //&& signs[i].isSign == "今日已打卡"
						// console.log(daysArr[j].date, day);
						daysArr[j].isSign = true;
					}
				}
			}
			this.days = daysArr;
		},

		// 切换控制年月，上一个月，下一个月
		handleCalendar(type) {
			const cur_year = parseInt(this.cur_year);
			const cur_month = parseInt(this.cur_month);
			var newMonth;
			var newYear = cur_year;
			if (type === 0) { //上个月
				newMonth = cur_month - 1;
				if (newMonth < 1) {
					newYear = cur_year - 1;
					newMonth = 12;
				}
			} else {
				newMonth = cur_month + 1;
				if (newMonth > 12) {
					newYear = cur_year + 1;
					newMonth = 1;
				}
			}
			this.calculateEmptyGrids(newYear, newMonth);
			this.calculateDays(newYear, newMonth);

			this.cur_year = newYear;
			this.cur_month = newMonth;

			this.SignUp = []; //先清空
			this.$emit('dateChange', this.cur_year+"-"+this.cur_month); //传给调用模板页面去拿新数据				
		},

		clickSignUp(date, type) { //0补签，1当日签到		
		
			var str = "签到";
			if (type == 0) {//如果不需要补签功能直接在这阻止不执行后面的代码就行。
				str = "补签";
			}
			uni.showToast({
				title: str + "成功" + date + "号",
				icon: 'success',
				duration: 2000
			});
			this.SignUp.push(this.cur_year + "-" + this.cur_month + "-" + date); //自动加假数据，写了接口就不用了
			
			this.$forceUpdate();
			
			this.$emit('clickChange', this.cur_year + "-" + this.cur_month + "-" + date); //传给调用模板页面


			//refresh
			this.onJudgeSign();

		}
	}
}
